body {
  font: 14px "Century Gothic", Futura, sans-serif;
  /* margin: 20px; */
}

.link {
  color: #05AAE1;
  text-decoration: underline;
  cursor: pointer;
}

.search {
  width: 30%;
  display: inline-block;
  vertical-align: top;
}
  .search input {
    width: 100%;
  }

  #studentList {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

    .studentRow {
      border: 1px solid #bbb;
      background-color: #ddd;
      padding: 5px;
      cursor: pointer;
    }

.itemsTable {
  width: 60%;
  display: inline-block;
  vertical-align: top;
  margin-left: 10%;
}
  .itemDropdown {
    margin-right: 3%;
  }